.overlay {
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color: rgb(0,0,0);
    background-color: rgba(0,0,0, 0.9);
    overflow-x: hidden;
    transition: 0.5s;
  }
  
  .overlay-content {
    position: relative;
    top: 8%;
    width: 100%;
    text-align: center;
    /* margin-top: 30px; */
  }
  
  .overlay a {
    padding: 8px;
    text-decoration: none;
    font-size: 30px;
    color: #818181;
    display: block;
    transition: 0.3s;
  }
  
  .overlay a:hover, .overlay a:focus {
    color: #fb0;
  }
  .is-active{
    color: #fb0 !important;
  }
  .overlay .closebtn {
    position: absolute;
    /* top: 20px; */
    right: 0px;
    /* font-size: 60px; */
  }
  .openNavClass{
      width: 100%;
  }
  .closeNavClass{
    width: 0%;
}
  @media screen and (max-height: 450px) {
    .overlay a {font-size: 20px}
    .overlay .closebtn {
    font-size: 40px;
    top: 15px;
    right: 35px;
    }
  }
  .hamburgger {
    color: black;
    float: left;
    margin: 15px;
    font-size: 20px;
    border: 2px solid transparent;
  }