.overlay {
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color: rgb(0,0,0);
    background-color: rgba(0,0,0, 0.9);
    overflow-x: hidden;
    -webkit-transition: 0.5s;
    transition: 0.5s;
  }
  
  .overlay-content {
    position: relative;
    top: 8%;
    width: 100%;
    text-align: center;
    /* margin-top: 30px; */
  }
  
  .overlay a {
    padding: 8px;
    text-decoration: none;
    font-size: 30px;
    color: #818181;
    display: block;
    -webkit-transition: 0.3s;
    transition: 0.3s;
  }
  
  .overlay a:hover, .overlay a:focus {
    color: #fb0;
  }
  .is-active{
    color: #fb0 !important;
  }
  .overlay .closebtn {
    position: absolute;
    /* top: 20px; */
    right: 0px;
    /* font-size: 60px; */
  }
  .openNavClass{
      width: 100%;
  }
  .closeNavClass{
    width: 0%;
}
  @media screen and (max-height: 450px) {
    .overlay a {font-size: 20px}
    .overlay .closebtn {
    font-size: 40px;
    top: 15px;
    right: 35px;
    }
  }
  .hamburgger {
    color: black;
    float: left;
    margin: 15px;
    font-size: 20px;
    border: 2px solid transparent;
  }
.btn-sq {
    width: 75px !important;
    height: 90px !important;
    margin: 10%;
    background-color:#fb0;
    color:#ffff;
    border-radius: 10px;
    border: 2px solid white;
    font-size: 35px;
    font-weight: 500;
    -webkit-animation-name:  btn-sq; /* Safari 4.0 - 8.0 */
    -webkit-animation-duration: 4s; /* Safari 4.0 - 8.0 */
    animation-name:  btn-sq;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
  }
  @-webkit-keyframes btn-sq {
    from {
      -webkit-transition: all 0.5s;
      transition: all 0.5s;
      -webkit-transform: rotate(90deg);
      transform: rotate(0deg);
    }
    to {
      -webkit-transition: all 0.5s;
      transition: all 0.5s;
      -webkit-transform: rotate(90deg);
      transform: rotate(360deg);
    }
  }
  @keyframes btn-sq {
    from {
      -webkit-transition: all 0.5s;
      transition: all 0.5s;
      -webkit-transform: rotate(90deg);
      transform: rotate(0deg);
    }
    to {
      -webkit-transition: all 0.5s;
      transition: all 0.5s;
      -webkit-transform: rotate(90deg);
      transform: rotate(360deg);
    }
  }
  .btn-sq-lg{
    width: 200px !important;
    height: 200px !important;
    margin: 10%;
    background-color:#fb0;
    color:#ffff;
    border-radius: 10px;
    border: 5px solid white;
    font-size: 100px;
    font-weight: 700;
  }
  .flip {
    -webkit-perspective: 800;   
            perspective: 800;
          position: relative;
          text-align: center;
  }
  .flip .card.flipped {
    -webkit-transform: rotatey(-180deg);
            transform: rotatey(-180deg);
  }
  .flip .card {
      height: 90vh;
      -webkit-transform-style: preserve-3d;
      -webkit-transition: 0.5s;
      transform-style: preserve-3d;
      transition: 0.5s;
     background-color: #000;
  }
  .flip .card .face {
    -webkit-backface-visibility: hidden ;
      backface-visibility: hidden ;
     z-index: 2;
  }
  .flip .card .front {
     position: absolute;
     /* width: 270px; */
     z-index: 1;
  }
  .flip .card .front img{
    width: 270px;
    height: 100%;
  }
  .flip .card .img {
     position: relaitve;
     width: 270px;
     height: 178px;
     z-index: 1;
     border: 2px solid #000;
  }
  .flip .card .back {
    padding-top: 10%;
    -webkit-transform: rotatey(-180deg);
            transform: rotatey(-180deg);
    /* position: absolute; */
  }
  .inner{
    margin:0px !important;
    /* width: 270px; */
  }

